#product-page {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  width: 100%;
  gap: 15px;
  min-height: 100vh;
}

#product-page h1 {
  width: 100%;
  text-align: center;
  margin: 0;
}
#product-page a {
  border-bottom: 1px solid black;
  width: 100%;
  text-align: center;
  padding-bottom: 9px;
  margin: 0;
  background-color: white;
}
#product-page img {
  /* background-color: white; */
  border-radius: 3px;
  /* box-shadow: 0 0 6px rgba(0, 0, 0, 0.3); */
  filter: drop-shadow(6px 3px 3px rgba(0, 0, 0, 0.3));
}
#product-page div {
  width: 100%;
  display: flex;
  flex-direction: column;
}
